import React, { useState, memo,useCallback} from "react";
import "./style.scss";
import Scroll from "components/Scroll/Scroll";

let userList = [
  { id: 1, icon: "iconfont icon-guanyu", text: "关于我们", right: "iconfont icon-arrow" },
  { id: 2, icon: "iconfont icon-lianxi", text: "联系我们",  right: "iconfont icon-arrow"},
  { id: 3, icon: "iconfont icon-yijianfankui-", text: "意见反馈", right: "iconfont icon-arrow" },
  { id: 4, icon: "iconfont icon-banben", text: "版本号", right: "iconfont icon-arrow" }
];
let mine = memo(props => {
  let [userInfo, setUserInfo] = useState(() => {
    return JSON.parse(localStorage.getItem("userInfo"));
  });

let toModifiedAction=useCallback(()=>{
  props.history.push('/mine/modified')
},[])

  return (
    <div className="page" id="mine">
      <header className="mine-top">
        <h1>我的</h1>
        <div className="scan">
          <i className="iconfont icon-shezhi"></i>
        </div>
      </header>

      <Scroll>
        <div className="cont">
          <div className="userCard" onClick={toModifiedAction}>
            <img
              src={userInfo.averImg || "https://m.baidu.com/se/static/wiseatom/personalcenter/assets/img/default_icon_02f13d8.png"}
              alt={userInfo.name}
            />
            <h3>{userInfo.name}</h3>
            <span>ID:{userInfo.id}</span>
            <p>{userInfo.desc || "这个用户好懒呀，啥都没说"}</p>
          </div>

          <div className="userFunc">
            <div className="city func">
              <i className="iconfont icon-bangzhu"></i>
              <span>帮助</span>
            </div>
            <div className="city func">
              <i className="iconfont icon-shoucang"></i>
              <span>收藏</span>
            </div>
            <div className="moments func">
              <i className="iconfont icon-ai-camera"></i>
              <span>圈子</span>
            </div>
          </div>

          <ul className="userList">

          {userList.map(item=>{
            return (
              <li key={item.id}>
              <i className="iconfont" className={item.icon}></i>
              <span>{item.text}</span>
              <span className={item.right}  ></span>
            </li>
            )
          })}

            
            
          </ul>
        </div>
      </Scroll>
    </div>
  );
});

export default mine;
